body{
    background-color: black;
}
h1{
    color: #fff;
    font-size: 100px;
}
.but{
    text-align: center;
    margin-top: 200px;
}

@keyframes moveInButton{
    0%{
        opacity:0;
        transform:translate(30px);
    }
    100%{
        opacity: 1;
        transform: translate(0);
    }

}
.btn:link,
.btn:visited{
    text-transform: uppercase;
    text-decoration: none;
    padding: 15px 40px;
    display: inline-block;
    border-radius: 100px;
    transition: all .2s;
    position: relative;
}

.btn:hover{
    transform:  translateY(-3px);
    box-shadow: 0 10px 20px rgb(255, 255, 255);
}

.btn:active{
    transform: translateY(-1px);
    box-shadow: 0 5px 10px rgb(255, 255, 255);
}
.btn-white{
    background-color: white;
    color: black;
}

.myVideo {
	position: absolute;
	right: 0;
	bottom: 0;
	z-index: -1;
  }

@media(min-aspect-ratio:16/9){
	.myVideo{
		width:100%;
		height:auto;
	}
}
@media(max-aspect-ratio:16/9){
	.myVideo{
		width:auto;
		height:100%;
	}
  }

.btn:after{
    content: "";
    display: inline-block;
    height: 100%;
    width: 100%;
    border-radius: 100px;
    position: absolute;
    top: 0;
    left: 0;
    z-index:-1;
    transition: all .4s;
}  

.btn-white::after{
    background-color: white;
}

.btn:hover::after{
    transform: scale(1.4) scale(1.6);
    opacity:0;
}

.btn-animated{
 animation: moveInButton .5s ease-out .75s;
 animation-fill-mode: backwards;
}